.image.image-all {
  width: 100%;
  height: 100%;
}
.image.image-padding {
  width: 100%;
  padding-bottom: 100%;
}
.image.image-half-padding {
  width: 100%;
  padding-bottom: 50%;
}
.image.image-radius {
  border-radius: 5px;
}
.image.image-circle {
  border-radius: 100%;
}
.image.image-center {
  margin: auto;
}
.image.no-overflow {
  overflow: unset;
}
.image .status {
  position: absolute;
  bottom: 0;
  right: 5px;
  background-color: #38c172;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  border: 2px #fff solid;
}
.image img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 100%;
}
.image .icn {
  position: "absolute";
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  color: #555;
}
.image .icn .icn-big {
  font-size: 34px;
}
.image .remove {
  position: absolute;
  top: 0;
  right: 0;
  width: 25px;
  height: 25px;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  cursor: pointer;
}
.image .choose {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  padding: 5px 0;
  background-color: rgba(0, 0, 0, 0.3);
  color: #fff;
  text-align: center;
  cursor: pointer;
}
.image .choose:hover {
  background-color: rgba(0, 0, 0, 0.5);
}
@media (max-width: 720px) {
  .image.image-center-mobile {
    margin: auto;
  }
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Graphik", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*::scrollbar {
  width: 6px;
  height: 6px;
  background-color: rgba(0, 0, 0, 0);
}
*::scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 1ex;
  border-radius: 1ex;
}
*::scrollbar-corner {
  background-color: #fff;
}
*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: rgba(0, 0, 0, 0);
}
*::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 1ex;
}
*::-webkit-scrollbar-corner {
  background-color: #fff;
}
*::-moz-scrollbar {
  width: 6px;
  height: 6px;
  background-color: rgba(0, 0, 0, 0);
}
*::-moz-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 1ex;
}
*::-moz-scrollbar-corner {
  background-color: #fff;
}
*:hover::scrollbar {
  width: 6px;
}
*:hover::-webkit-scrollbar {
  width: 6px;
}
*:hover::-moz-scrollbar {
  width: 6px;
}
*:hover::scrollbar-thumb {
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
  background-color: rgba(0, 0, 0, 0.3);
  width: 6px;
}
*:hover::-webkit-scrollbar-thumb {
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
  background-color: rgba(0, 0, 0, 0.3);
  width: 6px;
}
*:hover::-moz-scrollbar-thumb {
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
  background-color: rgba(0, 0, 0, 0.3);
  width: 6px;
}
html, body {
  height: 100%;
  -webkit-text-size-adjust: 100%;
}
a {
  text-decoration: none;
}
#app {
  font-family: "Graphik", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#app #header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background-color: #fff;
  -webkit-box-shadow: 3px 6px 18px #f9f9f9;
          box-shadow: 3px 6px 18px #f9f9f9;
  z-index: 100;
}
#app #header #header-container {
  width: 1100px;
  margin: auto;
}
@media (max-width: 720px) {
  #app #header #header-container {
    width: 100%;
  }
}
#app #logo {
  font-size: 30px;
  color: #FB3640;
  line-height: 1.5;
}
@media (max-width: 720px) {
  #app #logo {
    margin-left: 15px;
  }
}
#app #body {
